.grid__image {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(9, 1fr);
    height: 543px;
    min-width: min(100%, 747px);
    max-height: min(calc(100vw - 120px), 543px);
    column-gap: .5rem;
    row-gap: .5rem;
    border-radius: .5rem;
    overflow: hidden;
}

.grid__image-item {
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    transition: .5s ease-in-out;  
}


.grid__image-item:nth-child(1) {
    /* background-image: url('../../images/01.jpg'); */
    grid-row: 1 / 5;
    grid-column: 1 / 4;
}

.grid__image-item:nth-child(2) {
    /* background-image: url('../../images/02.jpg'); */
    grid-row: 1 / 7;
    grid-column: 4 / 7;

}

.grid__image-item:nth-child(3) {
    /* background-image: url('../../images/03.jpg'); */
    grid-row: 1 / 4;
    grid-column: 7 / 9;
}

.grid__image-item:nth-child(4) {
    /* background-image: url('../../images/04.jpg'); */
    grid-row: 5 / 10;
    grid-column: 1 / 4;
}

.grid__image-item:nth-child(5) {
    /* background-image: url('../../images/05.jpg'); */
    grid-row: 7 / 10;
    grid-column: 4 / 7;
}

.grid__image-item:nth-child(6) {
    /* background-image: url('../../images/06.jpg'); */
    grid-row: 4 / 7;
    grid-column: 7 / 9;
}

.grid__image-item:nth-child(7) {
    /* background-image: url('../../images/07.jpg'); */
    grid-row: 7 / 10;
    grid-column: 7 / 9;
}